












































import Vue from "vue";
import Component from "vue-class-component";
import * as store from "../plugins/store";
import { i18n } from "../plugins/i18n";
import * as api from "../utils/api";
import ResetDialog from "./ResetDialog.vue";
import KeysDialog from "./KeysDialog.vue";

@Component({
  components: {
    ResetDialog,
    KeysDialog
  }
})
export default class Navbar extends Vue {
  resetDialog = false;
  keysDialog = false;

  get locales() {
    return [{ icon: "🇺🇸", code: "en" }, { icon: "🇷🇺", code: "ru" }];
  }
  get currentLocale() {
    for (const locale of this.locales) {
      if (locale.code === i18n.locale) {
        return locale;
      }
    }
  }

  toggleMode() {
    store.setDark(!store.dark());
  }
  changeLanguage(locale: string) {
    i18n.locale = locale;
    store.setLanguage(locale);
    document.title = i18n.t("strippedTitle") as string;
  }
  logout() {
    store.logout();
    this.$router.replace("/");
  }
  toggleViewEdit() {
    store.setViewEditActive(!store.viewEditActive());
  }
  closeResetDialog() {
    this.resetDialog = false;
  }
  closeKeysDialog() {
    this.keysDialog = false;
  }
}
